  font-weight: 700;
  line-height: 1.31;
}

.mobile-menu__icon-close {
  margin-left: 20px;
}

.mobile-menu.is-open {
  transform: translateX(0);
}

.mobile-menu .mobile-menu__button-close {
  cursor: pointer;
  fill: #fff;
  background-color: #0000;
  border: none;
  position: absolute;
  top: 23px;
  right: 28px;
}

